import request from '@/static/request.js'
export default {
  data() {
    return {
      id: '',
      photo: '未填写',
      name: '未填写',
      wechat: '未填写',
      telephone: '未填写',
      gender: '',
      email: '未填写',
    }
  },
  mounted() {
    const vm = this
    request
      .get('schoolmateData/', {
        schoolmate_id: vm.id,
      })
      .then(res => {
        vm.name = res.data[0].user_name
        vm.gender = res.data[0].user_gender
        vm.telephone = res.data[0].user_telephone
        vm.email = res.data[0].user_email
        vm.wechat = res.data[0].user_weixinnumber
      })
  },
  onLoad(options) {
    uni.getStorage({
      key: 'GetId',
      success: res => {
        this.id = res.data
      }
    })
  },
  methods: {
    genderChange(e) {
      const vm = this
      vm.gender = e.currentTarget.dataset.gender
    },
    saveMessage() {
      const vm = this
      request
        .get('alterUserData/', {
          user_id: vm.id,
          user_name: vm.name,
          user_gender:vm.gender,
          user_weixinnumber: vm.wechat,
          user_email: vm.email,
        })
        .then(res => {
          if(res.data.statuscode === '400') {
            uni.showToast({
              title: '编辑失败',
              duration: 1000,
            })
          }
          else if(res.data.statuscode === '200') {
            uni.showToast({
              title: '编辑成功！',
              duration: 1000,
            })
            uni.reLaunch({
              url: '../user/user'
            })
          }
        })
    }
  },
}
